<template>
    <div>
        <!-- 구글 로그인: loginvue24-->
        <!-- 로그인 버튼 -->
        <button v-if="!isLoggedIn" @click="login" class="google-button">
            <img src="@/assets/googleloginbutton.png" alt="구글 로그인" class="google-button-img" />
        </button>
        <!-- 로그아웃 버튼 -->
        <button type="button" v-if="isLoggedIn" @click="logout" class="logout-button">logout</button>
    </div>
</template>
<script>
import { ref } from 'vue'; /* 상태관리를 위한 ref함수 */
import { googleSdkLoaded } from 'vue3-google-login';

/* 클라이언트 id 가져오기 */
const oauthClientId = process.env.VUE_APP_OAUTH_CLIENT;
/*  로그인 상태를 관리하는 반응형 변수 */
const isLoggedIn = ref(false);
/* 초기 로그아웃 상태 */ /* 구글 로그인 처리: 구글 sdk를 통해 oauth 인증하고, 인증 성공시 로그인 상태를 업데이트 */
const login = () => {
    /* googleSdkLoaded를 이용해 sdk가 로드되었는지 확인하고 콜백을 실행 */
    googleSdkLoaded((google) => {
        google.accounts.oauth2
            .initCodeClient({
                client_id: oauthClientId,
                scope: 'email profile openid',
                callback: (response) => {
                    /* 구글인증에 성공한 후 호출되는 콜백함수 */
                    console.log('응답을 처리합니다.', response);
                    isLoggedIn.value = true;
                }
            })
            .requestCode(); /* 인증코드 요청 */
    });
    /* 로그아웃 처리 */
    const logout = () => {
        console.log('user logged out');
        isLoggedIn.value = false;
    };
};

export default {};
</script>

<style scoped>
.google-button {
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
}

.google-button-img {
    width: 200px; /* 버튼 이미지 크기 */
    height: auto;
    display: block;
}

.google-button:hover {
    opacity: 0.9; /* 마우스 오버 시 효과 */
}

.logout-button {
    padding: 10px 20px;
    background-color: #4285f4;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
}

.logout-button:hover {
    background-color: #357ae8; /* 마우스 오버 시 색상 변경 */
}
</style>
